.message {
  font-family: Montserrat, Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
  padding-bottom: 10px;
  display: flex;
  justify-content: flex-end; /* сообщение справа */
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse; /* аватар справа от текста */
  gap: 25px;
}

.imageAndText img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  padding-bottom: 5px;
}

.text {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 414px;
  height: fit-content;
  flex-wrap: wrap;
  background-color: rgba(0, 102, 204, 1); 
  border-radius: 10px;
  padding: 10px 30px 10px 20px;
  box-sizing: border-box;
  color: white;

}

.text::after {
  content: "";
  position: absolute;
  bottom: 10px; /* = нижний padding */
  right: -20px; 
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 20px 0 0 30px;
  border-color: transparent transparent transparent rgba(0, 102, 204, 1);
  pointer-events: none;
}


.text::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: -20px; 
  width: 0;
  height: 0;
  border-style: solid ; 
  border-width: 20px 0 0 30px;
  border-color: transparent transparent transparent rgba(0, 102, 204, 1);
  pointer-events: none;
}

